  .homeButton {
    background-color: transparent; /* Green */
    border: 2px solid black;
    color: black;
    padding: 10px;
    text-align: center;
    font-size: 16px;
    cursor: pointer;
    border-radius: 50px;
    transition-duration: 0.4s;
  }

  .homeButton:hover{
      background-color: black;
      color: white;
  }

@-webkit-keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
